<template>
    <div class="m-card" id="app">
        <el-card class="box-card">
            <CardHeader />

            <main v-if="success == null" class="m-main">
                <form ref="loginForm">
                    <!-- 账号 -->
                    <div class="u-email">
                        <el-input
                            class="u-text u-email"
                            v-model="email"
                            placeholder="邮箱地址"
                            minlength="3"
                            maxlength="50"
                            @change="checkEmail"
                        >
                            <template slot="prepend">
                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" v-bind:class="'i-mail'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M467 61H45C20.218 61 0 81.196 0 106v300c0 24.72 20.128 45 45 45h422c24.72 0 45-20.128 45-45V106c0-24.72-20.128-45-45-45zm-6.214 30L256.954 294.833 51.359 91h409.427zM30 399.788V112.069l144.479 143.24L30 399.788zM51.213 421l144.57-144.57 50.657 50.222c5.864 5.814 15.327 5.795 21.167-.046L317 277.213 460.787 421H51.213zM482 399.787L338.213 256 482 112.212v287.575z"/></svg>
                            </template>
                        </el-input>
                        <div class="u-tip">
                            <el-alert
                                v-show="email_validate == false"
                                :title="email_validate_tip"
                                type="error"
                                show-icon
                                :closable="false"
                            ></el-alert>
                        </div>
                    </div>

                    <!-- 密码 -->
                    <div class="u-pass">
                        <el-input
                            class="u-text"
                            placeholder="输入密码"
                            v-model="pass"
                            show-password
                            @input="checkPass"
                        >
                            <template slot="prepend">
                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" v-bind:class="'i-pass'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M437.333 192h-32v-42.667C405.333 66.99 338.344 0 256 0S106.667 66.99 106.667 149.333V192h-32A10.66 10.66 0 0064 202.667v266.667C64 492.865 83.135 512 106.667 512h298.667C428.865 512 448 492.865 448 469.333V202.667A10.66 10.66 0 00437.333 192zM287.938 414.823a10.67 10.67 0 01-10.604 11.844h-42.667a10.67 10.67 0 01-10.604-11.844l6.729-60.51c-10.927-7.948-17.458-20.521-17.458-34.313 0-23.531 19.135-42.667 42.667-42.667s42.667 19.135 42.667 42.667c0 13.792-6.531 26.365-17.458 34.313l6.728 60.51zM341.333 192H170.667v-42.667C170.667 102.281 208.948 64 256 64s85.333 38.281 85.333 85.333V192z"/></svg>
                            </template>
                        </el-input>
                        <div class="u-tip">
                            <el-alert
                                v-show="pass_validate == false"
                                :title="pass_validate_tip"
                                type="error"
                                show-icon
                                :closable="false"
                            ></el-alert>
                        </div>
                    </div>

                    <!-- 提交 -->
                    <el-button
                        class="u-submit u-button"
                        type="primary"
                        @click="submit"
                        >登录</el-button
                    >
                </form>

                <LoginWith />

                <footer class="m-footer">
                    <p class="u-login">
                        还没有账号? <a :href="register_url">立即注册 &raquo;</a>
                    </p>
                    <p class="u-resetpwd">
                        <a href="../password_reset">忘记密码?</a>
                    </p>
                </footer>
            </main>

            <main v-if="success == true" class="m-main">
                <el-alert
                    title="登录成功"
                    type="success"
                    description="欢迎回来(#^.^#)"
                    show-icon
                    :closable="false"
                >
                </el-alert>
                <a
                    class="u-skip el-button u-button el-button--primary"
                    :href="redirect"
                    >{{ redirect_button }}</a
                >
            </main>

            <main v-if="success == false" class="m-main">
                <el-alert
                    title="登录失败"
                    type="error"
                    :description="errors"
                    show-icon
                    :closable="false"
                >
                </el-alert>
                <el-button
                    class="u-button u-submit"
                    type="primary"
                    @click="reset"
                    >返回</el-button
                >
            </main>
        </el-card>
        <Bottom />
    </div>
</template>

<script>
import CardHeader from "@/components/CardHeader.vue";
import LoginWith from "@/components/LoginWith.vue";
const { validator } = require("sterilizer");
const axios = require("axios");
const cookie = require("../../utils/cookie");
import { v4 as uuidv4 } from "uuid";
const { JX3BOX,User } = require("@jx3box/jx3box-common");
const API = JX3BOX.__server;
// const API = 'http://localhost:5120/'

export default {
    name: "Login",
    data: function() {
        return {
            success: null,

            redirect: "",
            redirect_button: "",
            errors: "未知异常",

            email: "",
            email_validate: null,
            email_validate_tip: "用户名有效长度为3-50个字符",

            pass: "",
            pass_validate: null,
            pass_validate_tip: "密码有效长度为6-50个字符",

            homepage: JX3BOX.__Root,

            failcount: 0,
            faillimit: 10,
            device_id: "",
        };
    },
    computed: {
        ready: function() {
            return this.email_validate && this.pass_validate;
        },
        register_url: function() {
            return "../register?redirect=" + this.redirect;
        },
    },
    methods: {
        checkEmail: function() {
            // 如果为空
            if (this.email == "") {
                this.email_validate = false;
                return;
            }

            // 校验格式
            let result = validator(this.email, {
                len: [3, 50],
            });
            this.email_validate = result;
        },
        checkPass: function() {
            // 如果为空
            if (this.pass == "") {
                this.pass_validate = false;
                return;
            }

            // 校验格式
            let result = validator(this.pass, {
                len: [6, 50],
            });
            this.pass_validate = result;
        },
        submit: function() {
            if (this.isfrozen()) return;

            // FIX:当使用填充器时,无法激活change事件,则提交时验证
            if (!this.ready){
                this.checkPass()
                this.checkEmail()
            }

            if (this.ready) {
                axios
                    .post(
                        API + "account/login/email",
                        {
                            user_login: this.email,
                            user_pass: this.pass,
                            device_id: this.device_id,
                        },
                        {
                            withCredentials: true,
                        }
                    )
                    .then((res) => {
                        this.success = true;

                        let data = res.data.data;
                        User.update(data).then(() => {
                            // 跳转至来源页
                            this.skip()
                        }).catch((err) => {
                            alert('浏览器版本太低,不支持本站')
                        })
                        
                    })
                    .catch((err) => {
                        console.log(err)
                        this.success = false;
                        if (err.response) {
                            this.errors = "账号或密码错误";
                            this.failcount++;
                            this.frozen();
                        } else {
                            this.errors = "网络异常或非法请求";
                        }
                    });
            }
        },
        reset: function() {
            this.success = null;
            this.email = "";
            this.email_validate = null;
            this.pass = "";
            this.pass_validate = null;
        },
        frozen: function() {
            if (this.failcount >= this.faillimit) {
                cookie.set("loginIsFrozen", "true", 86400);
            }
        },
        isfrozen: function() {
            if (this.failcount >= this.faillimit) {
                this.success = false;
                this.errors = "失败次数过多,请过24小时后再试";
                return true;
            }
            return false;
        },
        checkDirect: function() {
            let search = new URLSearchParams(document.location.search);
            let redirect = search.get("redirect");
            if (redirect) {
                this.redirect = redirect;
                this.redirect_button = "即将跳转";
            } else {
                this.redirect = this.homepage;
                this.redirect_button = "返回首页";
            }
            console.log(decodeURIComponent(this.redirect))
        },
        checkDeviceID: function() {
            if(localStorage){
                let device_id = localStorage.getItem("device_id");
                if (!device_id) {
                    this.device_id = uuidv4();
                } else {
                    this.device_id = device_id;
                }
                localStorage.setItem("device_id", this.device_id);
            }
        },
        skip : function (){
            if (this.redirect) {
                setTimeout(() => {
                    location.href = decodeURIComponent(this.redirect);
                }, 1200);
            }
        }
    },
    filters: {},
    mounted: function() {
        this.checkDirect();
        this.checkDeviceID();
    },
    components: {
        CardHeader,
        LoginWith
    },
};
</script>

<style lang="less">
@import "../../assets/css/common.less";
@import "../../assets/css/card.less";
@import "../../assets/css/register.less";
</style>
